body {
  background-color: #555;
}

select {
  color: #444;
}

.square {
  display: inline-block;
  height: 50px;
  width: 50px;
  background-color: #aaa;
  margin: 2px;
}

.square p {
  margin: 0;
  margin-left: 12px;
  font-size: 38px;
  color: #444;
}

.open {
  background-color: #ddd;
  font-weight: 900;
}

.marked {
  background: url('./assets/mark_flag.svg');
  background-color: #aaa;
}

.square.bomb.bombover {
  background: url('./assets/mark_bomb.svg');
  background-color: #aaa;
}

.square.marked.bomb.bombover {
  background: url('./assets/mark_flag.svg');
  background-color: #aaa;
}

.square.open.bomb {
  background: url('./assets/bomb.svg');
  background-color: #ddd;
}
.game-area {
  display: table;
  margin: 0 auto;
  color: #aaa;
  padding-top: 80px;
  user-select: none;
}

.game-area .restart-btn {
  text-align: center;
  padding-top: 25px;
}

.header {
  text-align: center;
}

.header:before {
  content: '\00a0 ';
}
